import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AuthGuard } from '@core/guards/auth.guard';
import { SeoGuard } from '@core/guards/seo/seo.guard';
import { RouteDataType } from '@core/services/route.service';

const routes: Routes = [
	{
		path: '',
		loadChildren: () => import('./pages/my-ad-list/my-ad-list.page.module').then((m) => m.MyAdListPageModule),
	},

	{
		path: APP_ROUTE.myAdmatchingTenants,
		loadChildren: () => import('./pages/my-ad-matching-tenant-list/my-ad-matching-tenant-list.module').then((m) => m.MyAdMatchingTenantListPageModule),
		canActivate: [AuthGuard, SeoGuard],
		data: {
			[RouteDataType.IS_HEADER_VISIBLE]: true,
			[RouteDataType.IS_FOOTER_VISIBLE]: false,
		},
	},
];

@NgModule({
	imports: [RouterModule.forChild(routes)],
	exports: [RouterModule],
})
export class MyAdsPageRoutingModule {}
